<template>
  <div class="listPage">
     <Header></Header>
		<h3>您的产品</h3>
		<table>
			<tr>
				<td v-for="item in listTitle">{{item}}</td>
			</tr>
			<tr v-for="item in totalList"> 
				<td>{{item.dingDanHao}}</td>	
				<td>{{item.newUseType}}</td>
				<td>{{item.goumai}}</td>
				<td>{{item.newUsetime}}</td>
				<td>{{item.buyDate}}</td>
				<td>{{item.number}}</td>
				<td>{{item.allMoney}}</td> 
			</tr>
		</table>
		<!-- <button @click='test'>test</button> -->
	<bottom></bottom>

  </div>
</template>

<script>
import Header from './header'
import bottom from './bottom'
export default {
  data () {
    return {
      listTitle:[],
      totalList:[]
    }
  },
  components:{
  		'Header':Header,
  		'bottom':bottom	
  },
  methods:{
  	test(){
  		// alert("yan-"+Math.random().toString().slice(3,10))
  		alert(new Date().getFullYear()+'年'+(new Date().getMonth()+1)+'月'+new Date().getDate()+'日'+new Date().getHours()
  	+'时'+new Date().getMinutes()+'分'+new Date().getSeconds()+'秒')
  	}
  },
  mounted(){
	this.$http.get('http://localhost:3000/title')
	.then((res)=>{
		console.log(res)
		this.listTitle=res.body
	}),
	this.$http.get('http://localhost:3000/moneyList')
	.then((res)=>{
		console.log(res)
		this.totalList=res.body
	})
}
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
table td{
	border-style: solid;
	color:white;
	text-align: center;
	padding: 2px;

}
table tr {
	background: green;
}
table{
	border: 10px groove red;
	margin:0 auto;
}
</style>



