<template>
	<transition name='fade'>
  <div class="childBank" v-if='isChlidShow'>
    <button @click="failHide">X</button>
	<div>
		<button @click="successShow=true">支付成功</button><button @click="failShow=true">支付失败</button>
		<div><h5 v-if="successShow" @click="successHide">支付成功</h5></div>
		<div><h5 v-if="failShow" @click="failHide">支付失败</h5></div>
	</div>

  </div>
</transition>
</template>

<script>
export default {
props:{
	isChlidShow:{
		type:Boolean,
		default:false
	},
	listTotal:{
		type:Object,
	}
},
  data () {
    return {
      successShow:false,
      failShow:false,
      dingDanHao:''
    }
  },
  methods:{
  	successHide:function(){
  		this.dingDanHao="yan-"+Math.random().toString().slice(3,10)
  		this.listTotal.goumai="购买产品"
  		this.listTotal.dingDanHao=this.dingDanHao
  		this.listTotal.buyDate=(new Date().getFullYear()+'年'+(new Date().getMonth()+1)+'月'+new Date().getDate()+'日'+new Date().getHours()
  	+'时'+new Date().getMinutes()+'分'+new Date().getSeconds()+'秒')
  		this.$emit('on-close')
  		this.successShow=false,
      this.failShow=false
      this.$http.post('http://localhost:3000/moneyList',this.listTotal)
      .then((res)=>{
      	console.log(res)
      	this.$router.push({path:'/listPage'})
      })
  	},
  	failHide(){
  		this.$emit('on-close')
  		this.successShow=false,
      this.failShow=false
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.childBank{
	position: absolute;
	left: 400px;
	top:300px;
	
	
}
.childBank>div{
	background: green;
	width: 300px;
	


}

div button{
	
	border:none;
	background: black;
	color:white;


	
}
div button:nth-child(2){
	margin-left:160px;

}
h5{
	background: black;
	color:white;
	text-align: center;
	padding: 2px;
	text-shadow: 3px 3px 5px red;
	cursor: pointer;
}
.fade-enter-active,.fade-leave-active{
	transition: all 0.5s ease-out;
}
.fade-enter,.fade-leave-to{
	transform: translateY(-500px);
}
</style>



